import React, { useEffect, useState } from 'react';
import DailyUtilizationChart from './DailyUtilizationChart';
import StatCards from './StatCards';
import { 
  fetchTodaysSchedules, 
  fetchTotalClasses, 
  fetchActiveLecturers, 
  fetchTotalRooms, 
  fetchTotalTimetables,
  fetchResourceUtilization
} from "../../utils/Services/dashboardService";

import { 
  Calendar, 
  Clock,
  Bell,
  BarChart2,
  PieChart,
  LogOut,
  UserCircle,
  ChevronDown
} from 'lucide-react';


const DashboardPage = () => {
  const [dashboardData, setDashboardData] = useState({
    schedules: [],
    totalClasses: 0,
    activeLecturers: 0,
    totalRooms: 0,
    totalTimetables: 0,
    resourceUtilization: []
  });
  const [isLoading, setIsLoading] = useState(true);
  const [currentDate, setCurrentDate] = useState('');
  const [showProfileMenu, setShowProfileMenu] = useState(false);

  useEffect(() => {
    // Set current date
    const now = new Date();
    setCurrentDate(now.toLocaleDateString('en-US', { 
      weekday: 'long', 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    }));

    const fetchData = async () => {
      setIsLoading(true);
      
      try {
        const [
          classes, 
          lecturers, 
          rooms, 
          timetables, 
          schedulesData,
          utilizationData
        ] = await Promise.all([
          fetchTotalClasses(),
          fetchActiveLecturers(),
          fetchTotalRooms(),
          fetchTotalTimetables(),
          fetchTodaysSchedules(),
          fetchResourceUtilization()
        ]);

        setDashboardData({
          schedules: schedulesData,
          totalClasses: classes,
          activeLecturers: lecturers,
          totalRooms: rooms,
          totalTimetables: timetables,
          resourceUtilization: utilizationData
        });
      } catch (error) {
        console.error("Error fetching dashboard data:", error);
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, []);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  return (
    <div className="bg-gray-50 min-h-screen">
      {/* Top Bar */}
      <div className="bg-white p-4 shadow-sm flex items-center justify-between">
        <div className="flex items-center">
          <h1 className="text-xl font-medium text-gray-800">Dashboard</h1>
        </div>
        <div className="flex items-center space-x-4">
          <button className="p-2 rounded-full hover:bg-gray-100 relative">
            <Bell size={20} />
            <span className="absolute top-0 right-0 h-2 w-2 bg-red-500 rounded-full"></span>
          </button>
          <div className="relative">
            <button 
              onClick={() => setShowProfileMenu(!showProfileMenu)} 
              className="flex items-center space-x-2 p-2 rounded-lg hover:bg-gray-100"
            >
              <div className="h-8 w-8 rounded-full bg-blue-500 text-white flex items-center justify-center">
                <span className="text-sm font-medium">JS</span>
              </div>
              <span className="text-sm text-gray-700 hidden md:block">John Smith</span>
              <ChevronDown size={16} className="text-gray-500" />
            </button>
            
            {showProfileMenu && (
              <div className="absolute right-0 top-full mt-1 w-48 bg-white rounded-lg shadow-lg py-1 z-10 border border-gray-100">
                <button className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center">
                  <UserCircle size={16} className="mr-2" />
                  Edit Profile
                </button>
                <button className="w-full text-left px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 flex items-center">
                  <LogOut size={16} className="mr-2" />
                  Log Out
                </button>
              </div>
            )}
          </div>
        </div>
      </div>

      <div className="container mx-auto p-4">
        {/* Welcome Section */}
        <div className="bg-white rounded-xl shadow-sm p-6 mb-6">
          <div className="flex justify-between items-center">
            <div>
              <h2 className="text-2xl font-semibold text-gray-800">Welcome back, Admin</h2>
              <p className="text-gray-500 text-sm">{currentDate}</p>
            </div>
            <div className="flex space-x-3">
              <button className="px-4 py-2 bg-gray-100 rounded-lg text-gray-700 text-sm hover:bg-gray-200 transition">
                Export Data
              </button>
              <button className="px-4 py-2 bg-blue-500 rounded-lg text-white text-sm hover:bg-blue-600 transition">
                Create Timetable
              </button>
            </div>
          </div>
        </div>

        {/* Stat Cards */}
        <StatCards dashboardData={dashboardData} />

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-6">
          {/* Today's Schedule */}
          <div className="bg-white rounded-xl shadow-sm p-6">
            <div className="flex justify-between items-center mb-4">
              <h3 className="font-semibold text-gray-800">Today's Schedule</h3>
              <div className="flex items-center text-sm text-blue-500">
                <Clock size={14} className="mr-1" />
                <span>{new Date().toLocaleDateString()}</span>
              </div>
            </div>
            <div className="space-y-3">
              {dashboardData.schedules.length > 0 ? (
                dashboardData.schedules.slice(0, 4).map((schedule, index) => (
                  <div key={index} className="flex items-center p-3 rounded-lg bg-gray-50 hover:bg-gray-100 transition-colors cursor-pointer">
                    <div className="w-12 text-center">
                      <span className="text-sm font-bold text-gray-800">{schedule.startTime || '10:00'}</span>
                      <p className="text-xs text-gray-500">AM</p>
                    </div>
                    <div className="ml-4 flex-1">
                      <h4 className="text-sm font-medium text-gray-800">{schedule.className || schedule.title || 'Computer Science'}</h4>
                      <p className="text-xs text-gray-500">
                        {schedule.lecturerName || 'Dr. Smith'} • Room {schedule.roomName || 'LH1'}
                      </p>
                    </div>
                    <div className={`h-2 w-2 rounded-full ${
                      index % 3 === 0 ? 'bg-green-500' : 
                      index % 3 === 1 ? 'bg-blue-500' : 'bg-purple-500'
                    }`}></div>
                  </div>
                ))
              ) : (
                <div className="text-center py-6">
                  <Calendar className="mx-auto h-10 w-10 text-gray-400" />
                  <p className="mt-2 text-sm text-gray-500">No classes scheduled for today</p>
                </div>
              )}
            </div>
            
          </div>

          {/* Daily Utilization */}
          <div className="bg-white rounded-xl shadow-sm p-6">
            <div className="flex justify-between items-center mb-4">
              <h3 className="font-semibold text-gray-800">Daily Utilization</h3>
              <div className="flex space-x-2">
                <button className="p-1 rounded hover:bg-gray-100">
                  <BarChart2 size={18} />
                </button>
                <button className="p-1 rounded hover:bg-gray-100">
                  <PieChart size={18} />
                </button>
              </div>
            </div>
            <DailyUtilizationChart />
          </div>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;