'use client';
import React, { useState, useEffect } from "react";
import { fetchFaculties, addFaculty, editFaculty, deleteFaculty } from './facultyService';

const FacultiesPage = () => {
  const [faculties, setFaculties] = useState([]);
  const [filteredFaculties, setFilteredFaculties] = useState([]);
  const [loading, setLoading] = useState(true);
  const [activeFaculty, setActiveFaculty] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [formData, setFormData] = useState({ facultyName: "" });
  const [feedback, setFeedback] = useState({ type: "", message: "" });
  const [activeCategory, setActiveCategory] = useState("All Faculties");

  useEffect(() => {
    const getFaculties = async () => {
      const data = await fetchFaculties();
      setFaculties(data);
      setFilteredFaculties(data);
      setLoading(false);
    };
    getFaculties();
  }, []);

  const handleInputChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
    setFeedback({ type: "", message: "" });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const apiCall = activeFaculty ? editFaculty(activeFaculty.facultyId, formData) : addFaculty(formData);
    const result = await apiCall;

    if (result.success) {
      setFeedback({ type: "success", message: activeFaculty ? "Faculty updated successfully!" : "Faculty added successfully!" });
      setTimeout(() => {
        setIsModalOpen(false);
        setActiveFaculty(null);
        setFormData({ facultyName: "" });
        setFeedback({ type: "", message: "" });
        const refreshFaculties = async () => {
          const data = await fetchFaculties();
          setFaculties(data);
          setFilteredFaculties(data);
        };
        refreshFaculties();
      }, 2000);
    } else {
      setFeedback({ type: "error", message: result.message });
    }
  };

  const handleEditFaculty = (faculty) => {
    setActiveFaculty(faculty);
    setFormData({ facultyName: faculty.facultyName });
    setIsModalOpen(true);
  };

  const handleDeleteFaculty = async (facultyId) => {
    try {
      await deleteFaculty(facultyId);
      const updatedFaculties = faculties.filter((faculty) => faculty.facultyId !== facultyId);
      setFaculties(updatedFaculties);
      setFilteredFaculties(updatedFaculties);
    } catch (error) {
      console.error("Error deleting faculty:", error);
    }
  };

  const openAddModal = () => {
    setActiveFaculty(null);
    setFormData({ facultyName: "" });
    setIsModalOpen(true);
  };

  return (
    <div className="flex flex-col min-h-screen p-6"> {/* Added flex and min-h-screen */}
      <div className="mb-6 flex justify-between items-center">
        <div>
          <h3 className="text-lg text-green-900 font-semibold">Faculties Management</h3>
          <p className="text-sm text-gray-500">Manage and monitor all available faculties</p>
        </div>
        <button
          onClick={openAddModal}
          className="px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700">
          Add New Faculty
        </button>
      </div>

      <div className="flex-1 bg-white rounded-lg overflow-hidden p-4"> {/* Added flex-1 */}
        <div className="p-4 border-b flex gap-4">
          {["All Faculties"].map((category) => (
            <button
              key={category}
              className={`px-3 py-1 rounded-md text-sm font-medium ${activeCategory === category
                  ? "bg-indigo-100 text-indigo-600"
                  : "bg-gray-100 text-gray-600 hover:bg-gray-200"
                }`}
              onClick={() => setFilteredFaculties(faculties)}
            >
              {category}
            </button>
          ))}
        </div>

        {loading ? (
          <div className="p-4 text-center text-gray-500">Loading faculties...</div>
        ) : (
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Faculty ID
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Faculty Name
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredFaculties.length > 0 ? (
                filteredFaculties.map((faculty) => (
                  <tr key={faculty.facultyId}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      {faculty.facultyId}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {faculty.facultyName}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <div className="flex gap-2">
                        <button
                          className="text-indigo-600 hover:text-indigo-900"
                          onClick={() => handleEditFaculty(faculty)}
                        >
                          Edit
                        </button>
                        <button
                          className="text-red-600 hover:text-red-900"
                          onClick={() => handleDeleteFaculty(faculty.facultyId)}
                        >
                          Delete
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan="3" className="px-6 py-4 text-center text-gray-500">
                    No faculties found.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        )}
      </div>

      {isModalOpen && (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center">
          <div className="bg-white rounded-lg p-6 w-full max-w-md">
            <h2 className="text-lg font-semibold mb-4">{activeFaculty ? "Edit Faculty" : "Add Faculty"}</h2>
            <form onSubmit={handleSubmit}>
              <div className="mb-4">
                <label htmlFor="facultyName" className="block text-sm font-medium text-gray-700 mb-1">
                  Faculty Name
                </label>
                <input
                  type="text"
                  id="facultyName"
                  name="facultyName"
                  value={formData.facultyName}
                  onChange={handleInputChange}
                  className="block w-full rounded-md border-gray-300 shadow-sm focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  required
                />
              </div>
              <div className="flex justify-end">
                <button type="button" onClick={() => setIsModalOpen(false)} className="mr-3 px-4 py-2 bg-gray-300 text-gray-700 rounded-md">
                  Cancel
                </button>
                <button type="submit" className="px-4 py-2 bg-indigo-600 text-white rounded-md">
                  {activeFaculty ? "Save Changes" : "Add Faculty"}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default FacultiesPage;
